"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { AlertCircle, Edit, StopCircle, Plus } from "lucide-react"
import { useContract } from "@/context/contract-context"
import { toast } from "sonner"
import { handleRpcError } from "@/lib/error-handler"
import { Separator } from "@/components/ui/separator"

export default function AdminTab({ campaignInfo, campaignId, onUpdate }) {
  const { contract } = useContract()
  const [isEndingCampaign, setIsEndingCampaign] = useState(false)
  const [newProposal, setNewProposal] = useState("")
  const [isAddingProposal, setIsAddingProposal] = useState(false)

  // End campaign early
  const handleEndCampaign = async () => {
    if (!contract || isEndingCampaign) return

    try {
      setIsEndingCampaign(true)

      // Call the contract's endCampaign function
      // Note: This function might not exist in your contract, you may need to implement it
      const tx = await contract.endCampaign(campaignId)

      toast.loading("Ending campaign...", {
        id: "end-campaign",
      })

      await tx.wait()

      toast.success("Campaign ended successfully!", {
        id: "end-campaign",
      })

      // Refresh data
      onUpdate()
    } catch (error) {
      console.error("Error ending campaign:", error)
      handleRpcError(error)
    } finally {
      setIsEndingCampaign(false)
    }
  }

  // Add new proposal
  const handleAddProposal = async (e) => {
    e.preventDefault()
    if (!contract || isAddingProposal || !newProposal.trim()) return

    try {
      setIsAddingProposal(true)

      // Call the contract's addProposal function
      const tx = await contract.addProposal(campaignId, newProposal)

      toast.loading("Adding proposal...", {
        id: "add-proposal",
      })

      await tx.wait()

      toast.success("Proposal added successfully!", {
        id: "add-proposal",
      })

      // Clear form and refresh data
      setNewProposal("")
      onUpdate()
    } catch (error) {
      console.error("Error adding proposal:", error)
      handleRpcError(error)
    } finally {
      setIsAddingProposal(false)
    }
  }

  // Check if campaign is active and can be modified
  const canModify = campaignInfo.status === 0 || campaignInfo.status === 1
  const canAddProposals = campaignInfo.votingType === 1 && canModify

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Edit className="h-5 w-5 text-purple-800 dark:text-purple-400" />
            Campaign Administration
          </CardTitle>
          <CardDescription>Manage your campaign settings and options</CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Campaign Status */}
          <div>
            <h3 className="text-lg font-medium mb-2">Campaign Status</h3>
            <div className="flex items-center gap-4">
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Current status:{" "}
                <span className="font-medium text-gray-900 dark:text-gray-100">
                  {campaignInfo.status === 0
                    ? "Created"
                    : campaignInfo.status === 1
                      ? "Active"
                      : campaignInfo.status === 2
                        ? "Ended"
                        : campaignInfo.status === 3
                          ? "Canceled"
                          : "Unknown"}
                </span>
              </div>

              {campaignInfo.status < 2 && (
                <Button
                  variant="destructive"
                  size="sm"
                  onClick={handleEndCampaign}
                  disabled={isEndingCampaign}
                  className="flex items-center gap-1"
                >
                  <StopCircle className="h-4 w-4" />
                  {isEndingCampaign ? "Ending..." : "End Campaign"}
                </Button>
              )}
            </div>
          </div>

          <Separator />

          {/* Add Proposals (only for proposal-based campaigns) */}
          {canAddProposals && (
            <div>
              <h3 className="text-lg font-medium mb-2">Add Proposal</h3>
              <form onSubmit={handleAddProposal} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="newProposal">Proposal Content</Label>
                  <Textarea
                    id="newProposal"
                    placeholder="Enter the proposal content"
                    value={newProposal}
                    onChange={(e) => setNewProposal(e.target.value)}
                    required
                    rows={3}
                  />
                </div>
                <Button
                  type="submit"
                  disabled={isAddingProposal || !newProposal.trim()}
                  className="flex items-center gap-1"
                >
                  <Plus className="h-4 w-4" />
                  {isAddingProposal ? "Adding..." : "Add Proposal"}
                </Button>
              </form>
            </div>
          )}

          {!canModify && (
            <Alert className="border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900">
              <AlertCircle className="h-4 w-4 text-amber-600 dark:text-amber-400" />
              <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2">
                This campaign has ended or been canceled and can no longer be modified.
              </AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
